import store from '@/store/index'
const V3_Meeting = {
    // 会议状态整理
    dataFormate (data) {
        if (data.memberId) {
            if (data.memberId.indexOf("@") > -1) {
                data.memberId = data.memberId.split("@")[0];
            }
            if (data.state & 0x000100 || data.state & 0x000001) { // 媒体在线 应用在线
                console.log(data.memberId, "媒体在线");
                data["Mstate"] = 4;
            } else if (data.state & 0x000200) {//用户拒绝邀请
                if (data.cause == "107408") { //107408 呼叫超时 （voip场景）,107603 被叫拒接 （voip场景）, 107781 用户拒接 （落地呼叫场景）,107775 无人接听 （落地呼叫场景）
                    data["Mstate"] = 6;//呼叫超时
                } else {
                    data["Mstate"] = 1; // 已拒绝
                }
                console.log(data.memberId, "已拒绝");
            } else if (data.state & 0x000400) {
                console.log(data.memberId, "正在接通");
                data["Mstate"] = 2; // 正在接通
            } else if (data.state & 0x00040000) {  //媒体超时
                console.log(data.memberId, "媒体超时");
                if (data.memberId === store.state.user_account) {
                    console.log('媒体超时, 退出会议');
                    this.$message.error(rxVue.$t('last.mediaTimeout'));
                    this.leaveMeeting();
                }
            } else if (data.state & 0x000800) {
                console.log(data.memberId, "已经挂断");
                data["Mstate"] = 3; // 已经挂断
            }
            // if ([7, 8, 9].indexOf(data.roleId) == -1) { // 7 遥控端创建者,8 遥控端主持人,9 遥控端成员
            //   data["state"] = 0;
            // }
        }
        return data
    },
    // 创建会议
    createMeeting (params, succFunc, errFunc) {
        let createMeetbuilder = new RL_MEET.CreateMeetBuilder();
        createMeetbuilder.setConfName(params.confName);
        createMeetbuilder.setConfRoomId(params.confRoomId);
        createMeetbuilder.setChatInConf(0); //九宫格会议传值为0,不会发送创建群组和退出群组的通知
        createMeetbuilder.setConfType(params.confType);
        createMeetbuilder.setVoiceMode(2);
        createMeetbuilder.setMembers(params.members);
        createMeetbuilder.setAutoClose(0);
        createMeetbuilder.setMaxMember(9);
        createMeetbuilder.setReserveEnable(params.reserveEnable);
        createMeetbuilder.setAppData(params.appData);
        RL_MEET.CreateMeet(createMeetbuilder, res => {
            console.log(res, '创建会议')
            succFunc && succFunc(res)
        }, err => {
            errFunc && errFunc(err)
        }
        );
    },
    // 邀请人员入会
    inviteMember (params, succFunc, errFunc) {
        const inviteMemberBuilder = new RL_MEET.InviteMemberBuilder();
        inviteMemberBuilder.setInviteMembers(params.memberslist);
        inviteMemberBuilder.setConfId(params.confId);
        inviteMemberBuilder.setCallImmediately(1);
        RL_MEET.InviteMember(inviteMemberBuilder, res => {
            console.info(res, "邀请成功")
            succFunc && succFunc(res)
        }, err => {
            console.log(err, "邀请失败")
            errFunc && errFunc(err)
        });
    },
    // 根据人员id拉取视频流
    cancelVideo (obj, succFunc, errFunc) {
        const cancelVideoBuilder = new RL_Media.cancelVideoBuilder();
        cancelVideoBuilder.setMediaInfo(obj.mediaInfo);
        cancelVideoBuilder.setCallId(RL_MEET.callId);
        cancelVideoBuilder.setCalled(obj.mediaInfo.confId);
        cancelVideoBuilder.setUserName(obj.userName);
        cancelVideoBuilder.setMemberId(obj.memberId);
        RL_Media.cancelVideo(cancelVideoBuilder, res => {
            console.log(res);
            succFunc && succFunc(res)
        }, err => {
            console.log(err);
            errFunc && errFunc(err)
        });
    },
    // 根据confid获取会议信息
    getMeetInfo (confId, succFunc, errFunc) {
        const GetMeetInfoBuilder = new RL_MEET.GetMeetInfoBuilder();
        GetMeetInfoBuilder.setConfId(confId);
        RL_MEET.GetMeetInfo(GetMeetInfoBuilder, res => {
            succFunc && succFunc(res)
        }, err => {
            errFunc && errFunc(err)
        })
    },
    // 获取参会人员列表
    getMeetMemberList (succFunc, errFunc) {
        RL_MEET.GetMeetMemberList(res => {
            succFunc && succFunc(res)
        }, err => {
            errFunc && errFunc(err)
        })
    },
    // 加入会议
    joinMeetRoom (params, succFunc, errFunc) {
        const joinMeetRoomBuilder = new RL_MEET.JoinMeetRoomBuilder();
        joinMeetRoomBuilder.setConfId(params.confId);
        joinMeetRoomBuilder.setUserName(params.userName);
        joinMeetRoomBuilder.setJoinType(params.joinType);
        RL_MEET.JoinMeetRoom(joinMeetRoomBuilder, (res, stream) => {
            succFunc && succFunc(res, stream)
        }, err => {
            errFunc && errFunc(err)
        })
    },
    // 开启声音
    startPublishVoice () {
        RL_MEET.StartPublishVoice(res => {
            console.log(res, "StartPublishvoice success");
        }, err => {
            console.error(err, "StartPublishvoice error");
        });
    },
    // 关闭声音
    stopPublishVoice () {
        RL_MEET.StopPublishVoice(res => {
            console.log(res, "StopPublishVoice success");
        }, err => {
            console.error(err, "StopPublishVoice error");
        });
    },
    // 开启视频
    startPublishVideo () {
        RL_MEET.StartPublishVideo(res => {
            console.log(res, "StartPublishVideo success");
        }, err => {
            console.error(err, "StartPublishVideo error");
        });
    },
    // 关闭视频
    stopPublishVideo () {
        RL_MEET.StopPublishVideo(res => {
            console.log(res, "StopPublishVideo success");
        }, err => {
            console.error(err, "StopPublishVideo error");
        });
    },
    // 离开会议
    leaveMeeting (succFunc, errFunc) {
        RL_MEET.leaveMeeting(res => {
            succFunc && succFunc(res)
        }, err => {
            errFunc && errFunc(err)
        });
    },
    //删除会议
    DeleteMeeting (confId,succFunc, errFunc) {
        RL_MEET.DeleteMeeting(confId,res => {
            succFunc && succFunc(res)
        }, err => {
            errFunc && errFunc(err)
        });
    },
    // 获取参会人员列表
    getMeetMemberList (succFunc, errFunc) {
        RL_MEET.GetMeetMemberList(res => {
            succFunc && succFunc(res)
        }, err => {
            errFunc && errFunc(err)
        })
    },
    // 拒绝邀请
    rejectInvitation (confId, succFunc, errFunc) {
        const rejectInvitationBuilder = new RL_MEET.RejectInvitationBuilder()
        rejectInvitationBuilder.setConfId(confId)
        rejectInvitationBuilder.setCause("107603")
        RL_MEET.RejectInvitation(rejectInvitationBuilder, res => {
            console.info(res, "拒绝邀请成功")
            succFunc && succFunc(res)
        }, err => {
            console.log(err, "拒绝邀请失败")
            errFunc && errFunc(err)
        });
    }
}
export default V3_Meeting



