<template>
  <div class="bg">
    <div class="con">
      <div class="selfInfo"
           v-if="toggleFlag">
        <dl>
          <dt class="headimg">
            <span class="img"
                  :style="{ backgroundImage: getcolor(LoginServe.UserId) }">{{LoginServe.Profile.NickName}}</span>
          </dt>
          <dd class="userName">{{LoginServe.Profile.NickName}}</dd>
        </dl>
        <div class="confirmBtn"
             @click.stop="switchAccount">退出登录</div>
      </div>
      <keep-alive>
        <router-view ref="headerChild"></router-view>
      </keep-alive>
    </div>
    <div class="shadow"
         v-if="addPersonFlag">
      <add-person :len="len"
                  :closeAddModal="addModelFlag"
                  :selected="selected"
                  :callback="callback"
                  :disabled="disabled"></add-person>
    </div>
    <!-- 会议界面 -->
    <div class="shadow v3InviteModel"
         v-if="v3InviteModel">
      <invite-model :inviteMsg="inviteMsg"></invite-model>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import utils from '@/utils/util'
import IM_Sdk_Listeners from '@/sdkBridge/listeners'
import addPerson from '../model/addPerson'
import inviteModel from '../meeting/inviteModel'
export default {
  name: 'yuntongxun',
  data() {
    return {
      toggleFlag: true,
      addPersonFlag: false,
      selected: [],
      len: 0,
      disabled: false,
      v3InviteModel: false,
      inviteMsg: {},
      parentModel: null,
    }
  },
  components: {
    addPerson,
    inviteModel,
  },
  computed: {
    ...mapState(['LoginServe']),
    ...mapGetters(['user_account']),
  },
  created() {
    const self = this
    let account = self.user_account
    if (!account) {
      let LoginServe = window.sessionStorage.getItem('LoginServe')
      if (!!LoginServe) {
        LoginServe =
          LoginServe && typeof LoginServe === 'string'
            ? JSON.parse(LoginServe)
            : LoginServe
        self.SetLoginServe(LoginServe)
        account = LoginServe.UserId
      } else {
        self.$router.replace('/')
      }
    }
    // if (account) {
    //   self.sdkInit((obj) => {
    //     console.log('初始化sdk及sdk登录', obj)
    //     self.SET_USERACCOUNT(account)
    //     // 用户状态监听
    //     IM_Sdk_Listeners.onConnectStateChange(self)
    //     // 接收消息监听
    //     IM_Sdk_Listeners.onMsgReceive(self)
    //     if (self.$route.query && self.$route.query.confId) {
    //       self.$bus.emit('initMeeting')
    //     } else {
    //       self.$bus.emit('updateMeetingList')
    //     }
    //   })
    // }
    if (self.LoginServe.Profile.init) {
      this.setIMNickname({ userName: '一个姓名' })
      // 初始化聊天会话sdk
      self.SET_USERACCOUNT(account)
      // 用户状态监听
      IM_Sdk_Listeners.onConnectStateChange(self)
      // 接收消息监听
      IM_Sdk_Listeners.onMsgReceive(self)
      if (self.$route.query && self.$route.query.confId) {
        self.$bus.emit('initMeeting')
      } else {
        self.$bus.emit('updateMeetingList')
      }
      // v3初始化
      RL_MEET.init(window.AppId, this.LoginServe.Profile.NickName)
    }
    self.setFontSize(100)
  },
  methods: {
    ...mapMutations(['SetLoginServe', 'SET_USERACCOUNT']),
    switchAccount() {
      RL_YTX_NEW.logout(
        (_) => {
          sessionStorage.setItem('LoginServe', '')
          this.$router.replace('/')
        },
        (err) => {
          this.$message.error(
            _this.$t('modelMessage.exitFailedCode') + err.code
          )
        }
      )
    },
    handleMsg(data) {
      console.log('handleMsg================================', data)
      let _this = this
      if (data.msgType == 50) {
        data.msgContent = _this.RX_Util.analyticBuilderDomain(data.msgContent)
        console.log('会议通知=============', data)
        if (
          data.msgContent.confId &&
          this.$route.query &&
          this.$route.query.confId
        ) {
          //  判断是否是当前会议的通知
          let obj = {
            ...data,
            msgContent: JSON.stringify(data.msgContent),
          }
          _this.$bus.emit('transferListenerData', 'MeetNotice_IM', obj)
          return
        }
        if (data.msgContent.noteType == 6) {
          _this.getMeetInfo(data, (defaultRes) => {
            let indexMember = defaultRes.members.filter(
              (item) => item.memberId == _this.user_account
            )
            if (indexMember.length > 0) {
              // 判断是否正在呼叫中
              if (indexMember[0].state & 0x000400) {
                _this.$bus.emit('inviteModelBool', true, data)
              }
            }
          })
        } else if (data.msgContent.noteType == 7) {
          if (
            data.msgContent.action == 56 &&
            data.msgContent.invitee == _this.user_account
          ) {
            if (
              data.msgContent.members.length &&
              data.msgContent.members[0].deviceType !== '21'
            ) {
              //会议被其他端拒绝
              _this.$message.info(_this.$t('last.callOtherCancel'))
            }
            _this.$bus.emit('inviteModelBool', false)
          } else if (
            data.msgContent.action == 153 &&
            data.msgContent.invitee == _this.user_account
          ) {
            _this.$message.info(_this.$t('last.inviteTimeout'))
            _this.$bus.emit('inviteModelBool', false)
          }
        }
      }
    },
    getMeetInfo(data, defaultMeetingCallback) {
      let _this = this
      const GetMeetInfoBuilder = new RL_MEET.GetMeetInfoBuilder()
      GetMeetInfoBuilder.setConfId(data.msgContent.confId)
      RL_MEET.GetMeetInfo(
        GetMeetInfoBuilder,
        (resp) => {
          console.log('会议信息', resp)
          resp = JSON.parse(resp.result)
          if (resp.statusCode == '000000') {
            defaultMeetingCallback && defaultMeetingCallback(resp)
          } else {
            _this.$message({
              message: '获取会议信息失败',
              type: 'warning',
            })
          }
        },
        (err) => {
          _this.$message({
            message: err,
            type: 'warning',
          })
        }
      )
    },
    sdkInit(cb) {
      let _this = this
      const account = this.LoginServe.UserId
      const resp = RL_YTX_NEW.init({
        appId: window.AppId,
        serverIp: window._ws,
        isV3: true,
      })
      if (resp.code != 200) {
        alert('SDK初始化错误')
        return
      } else if (174001 == resp.code) {
        alert('您的浏览器暂不支持,推荐使用谷歌浏览器^.^')
        return
      } else if (174002 == resp.code) {
        alert('您和服务器断开连接x_x')
        return
      }
      if (
        $.inArray(174004, resp.unsupport) > -1 ||
        $.inArray(174009, resp.usupport) > -1
      ) {
        alert('调用设备失败或浏览器不支持BLOB到URL转换')
      } else if ($.inArray(174007, resp.unsupport) > -1) {
        alert('浏览器不支持发送附件')
      } else if ($.inArray(174008, resp.unsupport) > -1) {
        alert('浏览器不支持音视频操作')
      }
      console.log('初始化OK~')
      let timestamp = utils.generateTimeNumber()
      let sig = hex_md5(window.AppId + account + timestamp + window.AppToken)
      let sdkLoginParam = {
        userName: account,
        type: 1,
        sig,
        timestamp,
        compId: 1,
      }
      _this.sdkLogin(sdkLoginParam, cb)
    },
    sdkLogin(param, cb) {
      // 初始化聊天会话sdk
      console.log('1.RL_YTX_NEW chatInit...')
      RL_YTX_NEW.chatInit({
        fileServerIp: window.fileServerIp,
        lvsServer: window.lvsServer,
      })
      console.log('2.RL_YTX_NEW setConfig...')
      console.log('3.RL_YTX_NEW login...')
      RL_YTX_NEW.login(
        param,
        (obj) => {
          console.log('RL_YTX_NEW login:', obj)
          RL_Chat = window.RL_Chat
          this.setIMNickname(param)
          cb && cb(obj)
        },
        (err) => {
          console.error('im登录失败。错误码:' + (err.msg || err.code))
        }
      )
      // v3初始化
      RL_MEET.init(window.AppId, this.LoginServe.Profile.NickName)
    },
    setIMNickname(userInfo) {
      RL_Chat.uploadPersonInfo(
        {
          nickName: userInfo.userName,
        },
        (obj) => {
          //设置成功
          console.log('聊天用户设置成功', obj)
        },
        (err) => {
          console.log(err)
        }
      )
    },
    toggleHeader(bool) {
      this.toggleFlag = bool
    },
    addModelFlag(bool, len, data, callback, disabled) {
      console.log('addModelFlag', data)
      this.addPersonFlag = bool
      this.selected = data
      this.len = len
      this.disabled = disabled
      this.callback = callback
    },
    inviteModelBool(bool, data) {
      this.v3InviteModel = bool
      this.inviteMsg = data
    },
  },
  mounted() {
    this.RX_Util.registerBusEvents(
      {
        toggleHeader: this.toggleHeader,
        addPerson: this.addModelFlag,
        inviteModelBool: this.inviteModelBool,
        handleMsg: this.handleMsg,
      },
      this
    )
  },
  updated() {
    const that = this
    const parentModel = this.$root.parentModel
    console.log(
      '*************************',
      that.v3InviteModel,
      parentModel,
      that.inviteMsg
    )
    if (that.v3InviteModel && parentModel) {
      const msgContent = that.inviteMsg.msgContent
      if (msgContent) {
        parentModel.source.postMessage(
          {
            type: 'meet',
            des: '邀请您参加' + msgContent.confName,
          },
          parentModel.origin
        )
      }
    }
  },
}
</script>

<style scoped lang="stylus">
div.bg
  width 100%
  height 100%
  position relative
  overflow hidden
  .con
    width 100%
    height 100%
    .selfInfo
      width 100%
      height 80px
      background-color $bgColorff
      display flex
      justify-content space-between
      align-items center
      padding 0 30px 0 20px
      box-sizing border-box
      dl
        flex 1
        padding 5px
        box-sizing border-box
        cursor pointer
        overflow hidden
        dt.headimg
          float left
          width 40px
          height 40px
          font 12px / 40px 'PingFangSC-Regular'
          text-align center
          color white
          margin-right 15px
          .img
            display inline-block
            text-align center
            width 40px
            height 40px
            color white
            line-height 40px
            overflow hidden
            font-size 14rem
            border-radius $borderRadiusSize
            overflow hidden
            img
              width 40px
              height 40px
        .userName
          font-size 18rem
          font-weight 500
          line-height 24px
          color $textColor11
          ellipsis()
      .confirmBtn
        width auto
        padding 0 10px
        background transparent
        border-radius 5px
        line-height 30px
        text-align center
        color $btnNormalColor
        cursor pointer
        border 1px solid $btnNormalColor
        margin-left 10px
</style>


