<template>
  <div class="container">
      <div class="main">
        <p class="title">
            <!-- {{inviteMsg}} -->
            <span class="time">{{inviteMsg.msgContent&& inviteMsg.msgContent.inviterName}}{{$t('last.meetInvite')}}{{inviteMsg.msgContent&& inviteMsg.msgContent.confName}}</span>
            <span class="btn"><i @click="meetInviteHangup">×</i></span>
        </p>
        <div class="btnGroup">
            <span class="rx_cancel cancel" @click="meetInviteHangup">{{$t('btn.refuse')}}</span>
            <span class="rx_btn confirm" @click="hangOn">{{hangOnText}}</span>
        </div>
      </div>
  </div>
</template>

<script>
  export default {
    name: "inviteModel",
    data() {
      return {
        hangOnText:this.$t('last.accept'),
        hasMeeting: false
      }
    },
    props:{
      inviteMsg: Object,
    },
    methods: {
      hangOn(){
        let _this = this
        //先挂断，再接听
        _this.$bus.emit('leaveAllMeet', true) //挂断会议
        setTimeout(function(){
          _this.$router.push({
            path: '/yuntongxun/meeting',
            query: {confId:_this.inviteMsg.msgContent.confId}
          })
          _this.$bus.emit('inviteModelBool',false)
        },500);
      },
      meetInviteHangup(){
        this.rejectInvitation(this.inviteMsg.msgContent.confId, res=>{}, err =>{});
        this.$bus.emit('inviteModelBool',false)
      },
      // 拒绝邀请
      rejectInvitation (confId, succFunc, errFunc) {
          const rejectInvitationBuilder = new RL_MEET.RejectInvitationBuilder()
          rejectInvitationBuilder.setConfId(confId)
          rejectInvitationBuilder.setCause("107603")
          RL_MEET.RejectInvitation(rejectInvitationBuilder, res => {
              console.info(res, "拒绝邀请成功")
              succFunc && succFunc(res)
          }, err => {
              console.log(err, "拒绝邀请失败")
              errFunc && errFunc(err)
          });
      },
    }
  }
</script>

<style scoped lang="stylus">
  .container{
    position relative
    width 100%
    height 100%
    .main{
        padding 20px
        width auto
        min-width 200px
        max-width 400px
        height auto
        overflow hidden
        z-index 1000
        background-color #fff
        color #333
        position fixed
        left 50%
        top 50%
        // transition all .5s
        -webkit-transform translateX(-50%) translateY(-50%)
        -moz-transform translateX(-50%) translateY(-50%)
        -ms-transform translateX(-50%) translateY(-50%)
        -o-transform translateX(-50%) translateY(-50%)
        transform translateX(-50%) translateY(-50%)
        border-radius: 8px;
        box-shadow: 0 2px 4px 0 rgba(0,0,0,0.15);
        .title{
            line-height 40px
            text-align center
            color #333
            .btn{
                position absolute
                right 0
                top 0
                i{
                    line-height 40px
                    padding 0 10px
                    display inline-block
                    color #666
                    font-size 18px
                    cursor pointer
                }
            }
        }
        .btnGroup {
          height 60px
          display flex
          align-items center
          justify-content space-between
          padding 0 20px
          .confirm,.cancel {
            padding 0 10px
            min-width 60px
            height 36px
            background $btnNormalColor
            border-radius 6px
            line-height 36px
            text-align center
            color $bgColorff
            cursor pointer
            margin 0 10px
          }
          .cancel{
              color #333
          }
      }
    }
  }
</style>



