/*
* sdk各类消息监听功能封装
* onConnectStateChange 用户状态监听
* onMsgReceive 接收消息监听
* onMsgNotifyReceive 消息阅后即焚、撤回、已读监听
* onGroupNoticeReceive 群组通知消息监听
* onCallMsg 音视频请求监听
* */
import Sdk_Callbacks from './sdkCallbacks'
let IM_Sdk_Listeners = {
  onConnectStateChange (vm) {
    window.parent.RL_YTX_NEW.onConnectStateChangeLisenter(obj => Sdk_Callbacks.onConnectStateChangeCallback(vm, obj))
  },
  onMsgReceive(vm){
    window.parent.RL_Chat.onMsgReceiveListener(msg => Sdk_Callbacks.onMsgReceiveCallback(vm, msg))
  },
  onMsgNotifyReceive(vm){
    window.parent.RL_Chat.onMsgNotifyReceiveListener(msg => Sdk_Callbacks.onMsgNotifyReceiveCallback(vm, msg))
  },
  onGroupNoticeReceive(vm){
    window.parent.RL_Chat.onNoticeReceiveListener(notice => Sdk_Callbacks.onGroupNoticeReceiveCallback(vm, notice))
  },
  onCallMsg(vm){
    window.parent.RL_Media.onCallMsgListener(msg => Sdk_Callbacks.onCallMsgCallback(vm, msg))
  },
  onLineStateNotifyListener(vm){
    window.parent.RL_Chat.OnLineStateNotifyListener(notice => Sdk_Callbacks.onLineStateNotifyListenerCallback(vm, notice))
  },
  InviteJoinMeetListener(vm){
    window.parent.RL_MEET.InviteJoinMeetListener(notice => Sdk_Callbacks.InviteJoinMeetListenerCallback(vm, notice))
  },
  MeetingListener(vm){
    window.parent.RL_MEET.MeetingListener(notice => Sdk_Callbacks.MeetingListenerCallback(vm, notice));
  },
  onConferenceNotifyLinstener(vm){
    window.parent.RL_Media.onConferenceNotifyLinstener(notice => Sdk_Callbacks.onConferenceNotifyLinstenerCallback(vm, notice));
  },
}

export default IM_Sdk_Listeners



