<template>
    <div class="addPerson">
        <p class="title">添加人员
            <span class="iconfont icon-popup_closex1 closeModal" @click.stop="closeAddModal(false)"></span>
        </p>
        <div class="container">
            <el-form :model="person" :rules="personRules" ref="personRef">
                 <el-form-item prop="account">
                    <el-input :disabled="personList.length == len" placeholder="请输入被邀请者账号" v-model.trim="person.account" class="input-with-select">
                        <el-button slot="append" class="rx_btn" @click.stop="addPerson">添加</el-button>
                    </el-input>
                 </el-form-item>
            </el-form>
            <ul class="list">
                <template v-for="item in personList">
                    <li :key="item.account">
                        {{item.account}}
                        <span class="delete" v-if="!item.disabled" @click.stop="deletePer(item.account)">x</span>
                    </li>
                </template>
            </ul>
            <div class="btn-group">
                <span class="rx_cancel cancel" @click.stop="closeAddModal(false)">{{$t('btn.cancel')}}</span>
                <span class="rx_btn confirm" @click.stop="callback(personList)">{{$t('btn.determine')}}</span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'addPerson',
        props:{
            closeAddModal: Function,
            selected: Array,
            len: Number,
            disabled: Boolean,
            callback:Function,
        },
        data() {
            
            return {
               person:{
                   account: '',
               },
               personList:[],
               personRules: {
                    account: [{
                        required: true,
                        message: "请输入账号",
                        trigger: 'blur'
                    }, {
                        min: 6,
                        max: 20,
                        message: '请输入6-20个字符的账号',
                        trigger: 'blur'
                    }]
                },
            }
        },
        methods: {
            addPerson(){
                if(this.personList.length == this.len) {
                    this.$message.warning('添加人数超过最大限制^_^')
                    return false
                }
                this.$refs["personRef"].validate(valid => {
                    if (valid) {
                        let {account} = this.person
                        const index = this.personList.filter(item=>item.account === account)
                        if(index.length > 0){
                            this.$message.warning('当前账号已添加^_^')
                            return false
                        }
                        this.personList.push({account:account,username: account})
                        this.person.account = ""
                    }
                })
            },
            deletePer(account){
                this.personList.forEach((item, index) => {
                    if(item.account == account){
                        this.personList.splice(index, 1);
                    }
                })
            }
        },
        mounted() {
            this.selected.forEach(item => {
                item.disabled = this.disabled
            })
            this.personList = [...this.personList, ...this.selected]
        }
    }
</script>

<style scoped lang='stylus'>
    .container >>> .el-input-group__append {
        background-color: $mainColor;
        color: white;
    }
    .addPerson{
        width: 50%;
        min-height:300px;
        background-color:#fff;
        border:1px solid $textColorCC;
        display flex
        flex-direction column
        font-size 14rem
        p.title {
            position relative
            width 100%
            height 52px
            text-align center
            font 16px / 56px "PingFangSC-Regular"
            color $textColor11
            box-shadow 0px 1px 2px 0px rgba(0,0,0,0.04);
            border-bottom solid 1px $bgColordf
            .closeModal {
                position absolute
                right 24px
                cursor pointer
                top -4px
                font-size 12px
            }
        }
        .container{
            padding:10px 15px;
            display flex
            flex-direction column
            flex 1
            .list{
                flex 1
                li{
                    float left
                    position relative
                    border solid 1px $bgColordf
                    border-radius 3px
                    line-height 24px
                    text-align center 
                    padding 0 10px
                    margin-right 10px
                    margin-bottom 10px
                    .delete{
                        position absolute
                        width 14px
                        height 14px
                        top: -5px;
                        right: -5px;
                        background-color:red;
                        color: #fff;
                        line-height:12px;
                        border-radius: 50%;
                        cursor pointer
                    }
                }
            }
            .btn-group{
                height 60px
                display flex
                padding 0px 42px
                align-items: center;
                border-top: solid 1px $bgColordf;
                .cancel {
                    width 270px
                    height 36px
                    border-radius 6px
                    border 1px solid $btnNormalColor
                    cursor pointer
                    color $btnNormalColor
                    text-align center
                    line-height 36px
                    margin-right 41px
                }
                .confirm {
                    width 270px
                    height 36px
                    background $btnNormalColor
                    border-radius 6px
                    cursor pointer
                    color $bgColorff
                    text-align center
                    line-height 36px
                }
            }
        }
    }
</style>


